<template>
  <div class="pay">
    <van-nav-bar title="提交订单" left-text="返回" left-arrow :fixed="true" @click-left="back" />
    <div class="bgcolor"></div>
    <div class="pay-siteBox">
      <van-cell is-link @click="showPopup">选择收货地址</van-cell>
      <van-popup v-model="isressshow" position="bottom" :style="{ height: '70%' }">
        <div v-if="list.lenght != 0">
           <van-address-list
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            @add="newAddress"
            @select="selectAddress"
            @edit="goaddress"
          />
        </div>
        <div v-else>
           <van-address-list
            v-model="chosenAddressId"
            :list="list"
            default-tag-text="默认"
            @add="newAddress"
            @select="selectAddress"
            @edit="goaddress"
          />
          <van-empty description="地址空空如也,快去添加吧" />
        </div>
      </van-popup>
      <div class="siteBox-text">
        <div class="clearfix site-box">
          <div class="fl site-name">{{currentAddress.name}}</div>
          <div class="fl site-phone">{{currentAddress.tel}}</div>
        </div>
        <div class="sites">{{currentAddress.address}}</div>
      </div>
      <div class="delivery">
        <van-cell is-link class="clearfix" @click="nowshowPopup">
          <div class="fl delivery-title">立即送出</div>
          <div class="fr delivery-time" v-if="currentTime == ''">请选择配送时间</div>
          <div class="fr delivery-time" v-else>大约 {{currentTime}} 送达</div>
        </van-cell>
        <van-popup v-model="istimeshow" position="bottom" :style="{ height: '30%' }">
          <van-datetime-picker
            v-model="currentTime"
            type="time"
            title="选择时间"
            :min-hour="0"
            :max-hour="23"
            @confirm="nowshowPopup"
          />
        </van-popup>
      </div>
    </div>
    <div class="shopbag">
      <div class="shopItem clearfix" v-for="( item , index ) in shopbagData" :key="index">
        <div class="shopbag-img fl">
          <img class="auto-img" :src="item.small_img" alt />
        </div>
        <div class="fl shopbag-right">
          <div class="shopbag-name">
            {{item.name}}
            <span>{{item.rule}}</span>
          </div>
          <div class="shopbag-enname">{{item.enname}}</div>
          <div class="clearfix shopbag-foot">
            <div class="fl shopbag-price">¥{{item.price}}</div>
            <div class="fr shopbag-count">X{{item.count}}</div>
          </div>
        </div>
      </div>
      <div class="dispatching">
        <div class="clearfix dispatching-box">
          <div class="dispatching-title">配送费</div>
          <div>
            <div class="fl dispatching-text">满35元免配送费</div>
            <div class="fr dispatching-price">¥{{shippingFee}}</div>
          </div>
        </div>
      </div>
      <div class="shopElse">
        <div class="shopElse-top">
          <!-- 优惠券单元格 -->
          <van-coupon-cell
            :coupons="coupons"
            :chosen-coupon="chosenCoupon"
            @click="showList = true"
          />
          <!-- 优惠券列表 -->
          <van-popup
            v-model="showList"
            round
            position="bottom"
            style="height: 90%; padding-top: 4px;"
          >
            <van-coupon-list
              :coupons="coupons"
              :chosen-coupon="chosenCoupon"
              :disabled-coupons="disabledCoupons"
              @change="onChange"
              @exchange="onExchange"
            />
          </van-popup>
        </div>
        <div class="delivery">
          <van-cell is-link class="clearfix" @click="onPayment">
            <div class="fl delivery-title">支付方式</div>
            <div class="fr delivery-time">{{Payment}}</div>
          </van-cell>
          <van-popup v-model="Paymentshow" position="bottom" :style="{ height: '50%' }">
            <van-radio-group v-model="Payment">
              <van-cell-group>
                <van-cell title="微信支付" clickable @click="Payment = '微信支付'">
                  <template #right-icon>
                    <van-radio name="微信支付" />
                  </template>
                </van-cell>

                <van-cell title="支付宝支付" clickable @click="Payment = '支付宝支付'">
                  <template #right-icon>
                    <van-radio name="支付宝支付" />
                  </template>
                </van-cell>

                <van-cell title="银行卡支付" clickable @click="Payment = '银行卡支付'">
                  <template #right-icon>
                    <van-radio name="银行卡支付" />
                  </template>
                </van-cell>
              </van-cell-group>
            </van-radio-group>
          </van-popup>
        </div>
        <div>
          <van-field
            v-model="message"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="50"
            placeholder="还有什么需要交代吗"
            show-word-limit
          />
        </div>
      </div>
    </div>
    <div>
      <van-submit-bar :price="product.total * 100" button-text="立即结算" @submit="onSubmit" />
    </div>
  </div>
</template>

<script>
const coupon = {
  available: 1,
  condition: "无使用门槛\n最多优惠12元",
  reason: "",
  value: 150,
  name: "优惠券名称",
  startAt: 1489104000,
  endAt: 1514592000,
  valueDesc: "1.5",
  unitDesc: "元",
};

import "../assets/less/pay.less";
import { utils } from "../assets/js/utils";
export default {
  data() {
    return {
      //总金额 
      price:0,
      // 选择配送时间
      currentTime: "",
      //支付方式
      Payment: "支付方式",
      Paymentshow: false,
      // 配送费
      shippingFee: 7,
      // 留言
      message: "",
      isressshow: false,
      istimeshow: false,
      showList: false,
      chosenCoupon: -1,
      coupons: [coupon],
      disabledCoupons: [coupon],
      //选择地址数据
      currentAddress: {
        name: "",
        tel: "",
        address: "",
      },
      // 地址
      chosenAddressId: "1",
      list: [],
      // 购物袋数据
      shopbagData: [],
      product: {
        count: 0,
        total: 0,
      },
      sids: [],
    };
  },
  created() {
    // 接收发送过来的数据
    this.sids = this.$route.query.sids.split("-");
    // 查询购物车
    this.getShopbagDataBySids();
    // 查询收货地址
    this.getReceiveAddress();
    //总价钱
    this.grossAmount();
  },
  methods: {
    //总金额
    grossAmount(){
      this.shopbagData.map( v => {
        
      })
    },
    // 配送时间

    //返回上一层
    back() {
      this.$router.go(-1);
    },
    //编辑地址
    goaddress() {
      this.$router.push({ name: "Address" });
    },
    //选择地址
    selectAddress(item, index) {
      this.isressshow = false;
      // 
      // 
      for (let key in this.currentAddress) {
        this.currentAddress[key] = item[key];
      }
    },
    //新增地址
    newAddress(item) {
      this.$router.push({ name: "NewAddress" });
    },
    //查询收货地址
    getReceiveAddress() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();
          // 
          if (result.data.code === 20000) {
            result.data.result.map((v) => {
              let address = v.province + v.city + v.county + v.addressDetail;
              // 

              this.list.push({
                id: v.aid,
                name: v.name,
                tel: v.tel,
                address,
                isDefault: Boolean(v.isDefault),
              });

              if (Boolean(v.isDefault)) {
                this.chosenAddressId = v.aid;
                this.currentAddress.name = v.name;
                this.currentAddress.tel = v.tel;
                this.currentAddress.address = address;
              }
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },
    //查询购物车
    getShopbagDataBySids() {
      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(this.sids),
        },
      })
        .then((result) => {
          this.$toast.clear();
          // 

          if (result.data.code == 50000) {
            result.data.result.map((v) => {
              this.product.count += v.count;
              this.product.total += v.count * v.price;
            });

            this.shopbagData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },

    //优惠券
    onChange(index) {
      // 
      this.showList = false;
      this.chosenCoupon = index;
    },
    onExchange(code) {
      // 
      this.coupons.push(coupon);
    },
    // 地址弹出层
    showPopup() {
      this.isressshow = !this.isressshow;
    },
    // 时间弹窗
    nowshowPopup() {
      this.istimeshow = !this.istimeshow;
    },
    // 支付弹窗
    onPayment() {
      this.Paymentshow = !this.Paymentshow;
    },
    //结算
    onSubmit() {
      // 判断是否选择了地址
      if (this.chosenAddressId == "") {
        this.$toast("请选择收货地址");
        return;
      }
      if (this.currentTime == "") {
        this.$toast("请选择收货时间");
        return;
      }
      if (this.Payment == "支付方式") {
        this.$toast("请选择支付方式");
        return;
      }

      // 获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      // 参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        sids: JSON.stringify(this.sids),
        phone: this.currentAddress.tel,
        address: this.currentAddress.address,
        receiver: this.currentAddress.name,
        // 支付方式
        Payment: this.Payment,
        // 配送时间
        currentTime: this.currentTime,
        // 配送费
        shippingFee: this.shippingFee,
        //留言
        message: this.message,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      this.axios({
        method: "POST",
        url: "/pay",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          // 
          this.$toast(result.data.msg);
          if (result.data.code == 60000) {
            this.$router.push({ name: "Order" });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          // 
        });
    },
  },
};
</script>